<template>
  <div class="order">
    <!-- 头部开始 -->
    <header style="line-height: 0.5rem">
      <Head>
        <div slot='a' class="he">
          <div class="left">
            <!-- <van-icon name="arrow-left" @click='$router.go(-1)' /> -->
            <a-Icon type="left" @click='$router.go(-1)' />
          </div>
          <div class="content">订单列表</div>
        </div>
      </Head>
    </header>
    <!-- 头部结束 -->

    <!-- 待支付订单开始 -->
    <div class="order_odd" v-for='(v,i) in arr' :key="i">
        <img :src="'//elm.cangdu.org/img/'+v.img" />
        <ul>
          <li>
              <div class="left">
                <div>效果演示</div>
                <div style="font-size: 0.14rem;color:gray">2019-09-08 18:39</div>
              </div>
              <div class="right" style="color: black">等待支付</div>
          </li>
          <li style="line-height: 0.8rem;">
              <div class="left" style="color: gray;font-size:0.16rem;">{{v.name}}</div>
              <div class="right" style="color: #DF6121;font-weight:600">￥{{v.price}}</div>
          </li>
        </ul>
        <div class="pay">去支付(还剩14分50秒)</div>
    </div>
    <!-- 待支付订单结束 -->


</div>
</template>

<script>
  import axios from 'axios'
  import Head from '@/page/header/Head'
  export default {
    name: 'Order',
    components:{Head},
    data() {
      return {
        arr:[]
      }
    },
    mounted() {
        axios.get('https://elm.cangdu.org/v1/user?user_id='+this.$store.state.login.id).then((res)=>{
          // console.log(res)
        })

        this.arr = this.$store.state.arr
        // console.log(this.arr)

    },
  }
</script>

<style lang="scss">
.order{
  width: 100%;
  .order_odd{
    width: 100%;
    padding:0.1rem 0.2rem;
    border-bottom:0.2rem solid #F1F1F1;
    background:white;
    display:flex;
    flex-wrap: wrap;
    &>img{
      width: 0.7rem;
      height: 0.7rem;
    }
    ul{
      width: 85%;
      margin-left: 0.2rem;
      li{
        height: 0.8rem;
        border-bottom:1px solid #F1F1F1;
        /* display:flex; */
        line-height:0.36rem;
        .left{
          float: left;
          font-size: 0.2rem;
          color: black;
        }
        .right{
          float: right;
        }
      }
    }
    .pay{
      width: 1.5rem;
      height: 0.23rem;
      border: 1px solid #E3B36B;
      color: #E3B36B;
      font-size: 0.14rem;
      text-align: center;
      line-height: 0.2rem;
      border-radius: 0.03rem;
      margin-top: 0.2rem;
      margin-left: 77%;
    }
  }
}  
</style>



